.tool-content__title {
  font-size: 22px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.45;
  letter-spacing: -0.3px;
  color: #23364e;
  margin-bottom: 16px;
}
.tool-content-item {
  margin-bottom: 40px;
}
.tool-content-wrp .tool-content-para {
  font-size: 14px;
  line-height: 26px;
  margin-bottom: 24px;
  font-weight: normal;
}
ul {
  list-style: none;
  margin-left: 2px;
}
.tool-content-listing p {
  margin-bottom: 16px;
  line-height: 26px;
}
.tool-content-listing ul li i {
  padding: 4px;
  padding-top: 0px;
}
.tool-content-listing ul li {
  margin-bottom: 12px;
  display: flex;
}
.tool-content-listing ul li span {
  line-height: 26px;
}
