.rangeSlider-contianer {
  width: 100%;
  margin-bottom: 56px;
}

.icon-tooltip .ant-tooltip-inner {
  position: relative;
  width: 146px;
  height: 50px;
  padding: 0px;
  background: #394a60;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  font-size: 12px;
  color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border: none;
}
.icon-tooltip .ant-tooltip-inner::after {
  content: "";
  position: absolute;
  border-style: solid;
  border-width: 5px 15px 0;
  border-color: #394a60 transparent;
  display: block;
  width: 0;
  z-index: 1;
  bottom: -5px;
  left: 57px;
}

.icon-tooltip .ant-tooltip-arrow-content {
  display: none;
}

.rangeSlider-contianer .label i {
  font-size: 19px;
  cursor: pointer;
}
.input-range {
  grid-column: 4 / 9;
  align-items: center;
}
.ant-slider {
  height: 8px;
  border-radius: 12px;
  background-color: #f2f5f7;
  padding: 0;
}

.ant-slider-track {
  background-color: #2ad25f;
  height: 8px;
  border-radius: 12px;
}

.ant-slider:hover .ant-slider-track {
  background-color: #2ad25f;
}
.ant-slider:hover .ant-slider-rail {
  background-color: #f2f5f7;
}

.ant-slider-handle {
  width: 20px;
  height: 20px;
  box-shadow: 0 2px 4px 0 rgba(14, 35, 224, 0.28);
  border: solid 5px #ffffff;
  background-color: #2ad25f;
}
.ant-slider-handle:focus {
  border: solid 5px #ffffff;
}
.ant-slider-handle.ant-tooltip-open {
  border: solid 5px #ffffff;
}
.ant-slider:hover .ant-slider-handle:not(.ant-tooltip-open) {
  border-color: #ffffff;
}

.ant-slider-dot {
  display: none;
}

