.ant-slider-tooltip .ant-tooltip-arrow-content {
  display: none;
}
.ant-slider-tooltip .ant-tooltip-inner {
  display: none;
}
.rangeSlider-contianer .ant-input-number {
  width: 72px;
}
.ant-input-number {
  border-radius: 4px;
  outline: none;
}
.ant-input-number:hover {
  border-color: var(--primary);
}

.ant-input-number-focused {
  border-color: var(--primary);
  box-shadow: none;
}
.input-range-slider {
  margin-right: 16px;
}

.ant-slider-with-marks {
  margin-bottom: 0px;
}
.ant-slider {
  margin-left: 0;
}
/* Media query */
/* >=568px */
@media only screen and (max-width: 1200px) {
  .input-range-slider {
    margin-right: 0px;
  }
  .slider-input-right {
    display: flex;
    justify-content: flex-end;
  }
}
