.slitpane-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-column-gap: 32px;
  margin-bottom: 32px;
}
.splitpane-full {
  grid-column: 1/13;
}
.splitpane-left {
  grid-column: 1/9;
}
.splitpane-right {
  grid-column: 9/13;
}
.sectionBorderStyle {
  border-radius: 4px;
  border: solid 1px #e3ebf1;
  background-color: #ffffff;
  padding: 32px;
}
@media only screen and (max-width: 768px) {
  .splitpane-full {
    margin-bottom: 24px;
  }
  .slitpane-container {
    grid-column-gap: 0px;
    margin-bottom: 0px;
  }
  .splitpane-left {
    grid-column: 1/13;
    margin-bottom: 24px;
  }
  .splitpane-right {
    grid-column: 1/13;
    margin-bottom: 24px;
  }
}

@media only screen and (max-width: 576px) {
  .sectionBorderStyle {
    padding: 24px;
  }
}
