.hero-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}
.hero-content {
  grid-column: 1/8;
}
.hero-content p {
  line-height: 26px;
}
.hero-image {
  grid-column: 10/13;
  margin: -32px;
}
.hero-image img {
  object-fit: cover;
  height: 194px;
  width: 100%;
}

@media only screen and (max-width: 768px) {
  .hero-content {
    grid-column: 1/13;
  }
  .hero-content p {
    line-height: 26px;
  }
  .hero-image {
    display: none;
  }
}
